import YButton1 from './src/components/button1/button.vue';
import YButton2 from './src/components/button2/button.vue';
import _Vue from 'vue'
[YButton1,YButton2].map(item=>{
  item.install = Vue => {
    if (!Vue) {
      window.Vue = Vue = _Vue
    }
    Vue.component(item.name, item)
  }
})
export {
  YButton1,YButton2
}